html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  background-image: url(https://wallpapers.com/images/high/best-marble-background-6v2qysqi0lvrsu40.webp);
  opacity: 0.7;
  background-size: cover;
  font-family: "Lato", sans-serif;
  margin: 20px;
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
    padding: 20px;
}

.form__input {
    padding: 10px;
    border: 1px solid #ddd;
}

.form__button {
    border: none;
    background-color: teal;
    color: white;
    padding: 10px 20px;
}

.form__button:hover {
    cursor: pointer;
    background-color: hsl(180, 100%, 30%);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all .2s;

}

@media (max-width: 600px) {
    .form {
        display: flex;
        flex-direction: column;
    }
}
.list {
    padding: 30px 10px;
    margin: 0px;
    list-style: none;
}

.list__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    padding: 15px;
    align-items: center;
    border-bottom: 1px solid #ddd;
}

.list__item--hidden {
    display: none;
}

.list__content--done {
    text-decoration: line-through;
}

.list__button {
    width: 35px;
    height: 35px;
    color: white;
    border: none;
}

.list__button--done {
    background-color: green;
}

.list__button--done:hover {
    background-color: hsl(120, 100%, 30%);
    transition: .2s;
    cursor: pointer;
}

.list__button--remove {
    background-color: red;
}

.list__button--remove:hover {
    background-color: hsl(0, 100%, 65%);
    transition: .2s;
    cursor: pointer;
}
@media (max-width: 600px) {
    .header__title {
        display: flex;
        justify-content: center;
    }
}
.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
}

@media (max-width: 600px) {
    .container {
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: center;
    }
}

.container__header {
    margin: 0;
}

@media (max-width: 600px) {
    .container__header {
        justify-content: start;
    }
}

.container__button {
    border: none;
    background-color: white;
    color: teal;
    cursor: pointer;
}


.container__button:hover {
    color: hsl(180, 100%, 35%);
    transition: .2s;
}

.container__button:disabled {
    color: #ccc;
    transition: .2s;
    cursor: auto;
}
.section {
    background-color: white;
    margin: 20px 0;
}

.section__header {
    border-bottom: 1px solid #ddd;
    padding: 20px;
    margin: 0;
}

.header__title {
    justify-content: flex-start;
}

.section__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 20px;
    margin: 0;
}

@media (max-width: 600px) {
    .section__container {
        display: grid;
        grid-template-columns: 1fr;
        grid-row-gap: 20px;
        row-gap: 20px;
    }
}
.main {
    margin: 50px auto;
    max-width: 1000px;
    word-break: break-word;
}

@media (max-width: 600px) {
    .main {
        margin: 20px
    }
}
